class ModalLayer {
    constructor(opt) {
        this.opt = opt;
        this.modal = null;
    }
    show() {
        this.modal = document.querySelector('.modal[data-id="'+ this.opt.id +'"]');

        const baseLayer = document.querySelector('.base-layer');
        const wrapper = document.querySelector('.wrapper');
        baseLayer.dataset.layer = 'true';
        wrapper.inert = 'true';

        if(!this.modal){
            console.error(`modal with id '${this.opt.id}' not found`);
            return; // modal 없으면 종료
        }
        
        const el_close = this.modal.querySelector('.modal-close');
        const isPageModal = this.modal.closest('.base-layer');
        const modal_body = this.modal.querySelector('.modal-content');
        modal_body.scrollTo({
            top:0 
        });

        if (isPageModal) {
            document.body.classList.add('is-modal');
        }
        this.modal.setAttribute('aria-hidden', false);

        setTimeout(() => {
            this.opt.callback && this.opt.callback();
        },0);

        const el_openModals = document.querySelector('.modal[aria-hidden="false"]');
        const compStyle = window.getComputedStyle(el_openModals);
        const zIndex = compStyle.zIndex;
        
        if(el_openModals) {
            el_openModals.style.zIndex = zIndex * 1;   
        }

        !!el_close && el_close.focus();
    }
    hide () {
        const baseLayer = document.querySelector('.base-layer');
        const wrapper = document.querySelector('.wrapper');
        baseLayer.dataset.layer ='false';
        wrapper.inert = '';
        
        
        if(this.modal) {
            document.body.classList.remove('is-modal');
            this.modal.removeAttribute('aria-hidden');
            this.modal.setAttribute('aria-hidden', true);
            this.modal.removeAttribute('data-current');
            this.modal.removeAttribute('style');
        }
    }
}

export default ModalLayer;