<template>
    
    <div class="subpage">
    
        <div class="btn-list">
            <button type="button" @click="openModal('term')" class="btn" data-btn="modal">Full 팝업</button>
            <button type="button" @click="openModal('alarm')" class="btn" data-btn="modal">alert 팝업</button>
            <button type="button" @click="openModal('info')" class="btn" data-btn="modal">bottom 팝업</button>
        </div>
        
        <ModalItem 
            :data-list="{id: 'term', type: 'full', visible: modals.alarm.visible}" 
            :modal-popup="modals.term" 
            @close="closeModal('term')"
        >
            <template #head>
                <h1 class="modal-tit">개인정보 수집 및 이용 동의(필수)</h1>
            </template>
            <template #body>
                <div class="terms-wrap">
                    <div class="mdl-box">
                        <h2 class="tit-terms">1. 수집ㆍ이용 목적</h2>
                        <p>건강정보 조회/분석결과 및 예측서비스</p>
                        <h2 class="tit-terms">2. 수집ㆍ이용 항목</h2>
                        <strong>검진결과[(검진일, 검진기관명), 신체계측(신장, 체중, 허리둘레, 체질량지수(BMI), 수축/이완기 혈압, 시력, 청력), 혈액검사(혈색소, 공복시혈당), 상복부검사(AST, ALT, 감마지티피), 신장요로계검사(요단백, 혈청크레아티닌, 신사구체여과율), 순환기계검사(총콜레스테롤, HDL콜레스테롤, LDL콜레스테롤), 폐결핵흉부질환검사(흉부방사선촬영), 근골격계검사(골다공증)], 진료 및 투약정보(병원명, 방문일수, 처방횟수, 투약횟수, 처방약품정보), 뇌졸중/당뇨병/심내혈관질환 예측결과 </strong>
                        <p class="mdl-bul-star">나이마다 검진결과 항목이 상이할 수 있음</p>
                        <h2 class="tit-terms">3. 보유 및 이용기간</h2>
                        <strong>건강정보결과 창 종료 또는 로그아웃 시 파기</strong>
                    </div>
                    <div class="mdl-bul-care">
                        <div> 민감정보 수집 및 이용동의를 거부할 권리가 있으며, 거부할 경우에는 건강정보 조회/분석결과 및 예측서비스가 제한됩니다. </div>
                    </div>
                </div>
            </template>
            <template #foot>
                <button type="button" class="btn" data-style="base-line" @click="closeModal('term')"><span>미동의</span></button>
                <button type="button" class="btn" data-style="base"><span>동의</span></button>
            </template>
        </ModalItem>


        <ModalItem 
            :data-list="{id: 'alarm', type: 'alert', visible: modals.alarm.visible}" 
            :modal-popup="modals.alarm" 
            @close="closeModal('alarm')"
        >
            <template #head>
                <h1 class="modal-tit">알림</h1>
            </template>
            <template #body>
                <p>기상하셨나요?</p>
            </template>
            <template #foot>
                <button type="button" class="btn" data-style="base-line" @click="closeModal('alarm')"><span>취소</span></button>
                <button type="button" class="btn" data-style="base"><span>확인</span></button>
            </template>
        </ModalItem>


        <ModalItem 
            :data-list="{id: 'info', type: 'bottom', visible: modals.alarm.visible}" 
            :modal-popup="modals.info" 
            @close="closeModal('info')"
        >
            <template #head>
                <h1>혈당 관리를 위한 식단 관리법</h1>
            </template>
            <template #body>
                <p>
                    간헐적 단식은 단순히 식사를 걸러서 하는 것이 아니다. 
                    일정한 시간 동안 금식을 통하여 인슐린 분비를 조절하는 것이다. 
                    하루 금식 시간을 12시간에서 16시간 이상 유지하여 인슐린의 과도한 분비를 억제하면, 
                    인슐린으로 인한 혈당의 지방전환을 억제할 뿐 아니라, 
                    필요한 에너지를 포도당이 아닌 지방을 분해하여 에너지원으로 사용하게 되어 체중조절 등에 도움이 된다.
                </p>
                <div class="info-txt-wrap">
                    <ul>
                        <li>공복혈당과 식사 시작 후 2시간 혈당을 확인하세요.</li>
                        <li>혈당 관리를 시작한 경우, 아침, 점심, 저녁의 식전과 식후 혈당을 모두 확인하세요.</li>
                    </ul>
                </div>
            </template>
            <template #foot>
                <button type="button" class="btn" data-style="base" @click="closeModal('info')"><span>확인</span></button>
            </template>
        </ModalItem>
    </div>
    
</template>
<script setup>
import ModalItem from '@/components/ModalItem'
import ModalLayer from '@/assets/js/ModalLayer'

const modals = {
    term: {
        layer: new ModalLayer({ id: 'term' }),
        visible: true,
        callback: () => {
            console.log('modal Show 111111111');
        }
    },
    alarm: {
        layer: new ModalLayer({ id: 'alarm' }),
        visible: true,
        callback: () => {
            console.log('modal Show 22222222');
        }
    },
    info: {
        layer: new ModalLayer({ id: 'info' }),
        visible: true,
        callback: () => {
            console.log('modal Show 33333333');
        }
    },
}

const openModal = (modalID) => {
    const modal = modals[modalID];
    //console.log(modal.opt.id);
    if(modal){
        modal.layer.show();
    }
}

const closeModal = (modalID) => {
    const modal = modals[modalID];
    if(modal) {
        modal.layer.hide();
    }
};
</script>